<template>
  <div class="propertyOrderPay" :class="{themeL:bindThemeL}">
    <!-- 头部 -->
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth" >
    </navigation-bar>
    <div class="propertyOrderPayContent">
      <van-swipe indicator-color="white" :show-indicators="false">
        <van-swipe-item v-for="(room,roomIndex) in orderDetial.roomList" :key="roomIndex">
          <div class="user_info">
            <van-image class="user_img" :src="houseImg" />

            <div class="user_word">
              <div class="user_home">{{room.room_name}}</div>
              <div class="user_name">房主：{{room.owner_name}}</div>
            </div>
          </div>
          <div class="order_itme_list">
            <div v-for="(x,index) in room.monthList" :key="index">
              <div
                style="display: flex;height: 60px;line-height: 60px;border-bottom: 1px solid #ececec;margin: 0 5%;"
                @click="showTime(roomIndex,index)"
              >
                <div style="width: 50%;font-size:14px;" :style="{fontSize:itemFont}">{{x.month_name}}</div>
                <div
                  style="width: 50%;text-align: right;font-size:14px;"
                  :style="{fontSize:itemFont}"
                >￥{{x.month_amount| keepTwoNum}}</div>
                <van-image
                  width="14"
                  height="14"
                  style="margin-top: 23px"
                  :src="x.isShow?arrowDownImg:arrowRightImg"
                />
              </div>
              <div v-if="x.isShow" style="padding-bottom: 16px; background: #f7f7f7;">
                <div
                  class="freeShow"
                  v-for="(item,monthIndex) in x.billDetailList"
                  :key="monthIndex"
                >
                  <div class="freeText">{{item.payment_type}}</div>
                  <div class="freeText">￥{{item.payment_amount}}</div>
                </div>
              </div>
            </div>
          </div>
        </van-swipe-item>
      </van-swipe>

      <div class="pointDeduction">
        <div class="change_point">
          <img class="delete_icon" :src="cutImg" @click="numberReduce(pointAviable)" />
          <input type="number" class="point_num" v-model="integralDeduction" />
          <img class="add_icon" :src="addImg" @click="numberAdd(pointAviable)" />
        </div>
        <div class="point_info">
          <div class="point_title">积分抵扣</div>
          <div class="point_rule">100积分抵扣1元</div>
        </div>
      </div>
      <div class="line"></div>
      <div class="pointDeduction">
        <van-image
          class="choose_is_point"
          :src="integralAll?switchOnImg:switchOffImg"
          @click="integralAllFn(pointAviable)"
        />
        <div class="point_info">
          <div class="point_title">
            <span>使用全部积分抵扣</span>

            <div style="width: 25px;margin-top: -3px;" @click="integralInstructionFn()">
              <van-image :src="tipsImg" />
            </div>
          </div>
          <div class="point_rule">本次可用最高积分为{{pointAviable}}</div>
        </div>
      </div>
      <div class="line"></div>
      <div class="total_and_deduction">
        <div class="total_money">
          <div class="total_name">订单总额</div>
          <div class="total_num">￥{{bill_amount| keepTwoNum}}</div>
        </div>
        <div class="deduction_point">
          <div class="deduction_name">积分抵扣</div>
          <div class="deduction_num">-￥{{pointAviablePersent| keepTwoNum}}</div>
        </div>
      </div>
    </div>
    <div style="height:87px;"></div>
    <property-order-goto-pay :amount="billCodePoint" @goToPay="goToPay"></property-order-goto-pay>
    <property-pay-popup
      v-if="payWindowShow"
      :payParam="payParam"
      :isAlipay="isAlipay"
      :isWechart="isWechart"
      @closePopup="closePopup"
      @checkOrderStatus="checkOrderStatus"
    ></property-pay-popup>
  </div>
</template>

<script>
import {
  queryOrderDetailService,
  getUseLimitService,
  queryPaymentDetailService,
  payOrderService
} from "../../../services/propertyBillService";
import propertyOrderGotoPay from "./propertyOrderGotoPay";
import propertyPayPopup from "./propertyPayPopup";
import NavigationBar from '@/common/currency/NavigationBar.vue';
import Vue from "vue";
import { Dialog } from "vant";
Vue.use(Dialog);
export default {
  name: "propertyOrderPay",
  data() {
    return {
      billNo: "",
      areaCode: "",
      bill_amount: 0, //订单金额
      billCodePoint: 0, //积分抵扣后的订单金额
      pointAviablePersent: 0, //积分抵扣
      pointAviable: 0, //可用积分
      integralAll: false, //是否使用全部积分
      integralDeduction: 0, //使用的积分值
      orderDetial: {},
      payWindowShow: false, //支付弹窗
      isAlipay: false,
      isWechart: false,
      needRefresh: true,
      headerItemWidth:{
          width:'84px'
      },
      title:'订单支付',
      switchOnImg: require("../../../assets/images/home/property/payment/switch_on.png"),
      switchOffImg: require("../../../assets/images/home/property/payment/switch_off.png"),
      houseImg: require("../../../assets/images/home/property/payment/house_house.png"),
      addImg: require("../../../assets/images/home/property/payment/bg_add.png"),
      cutImg: require("../../../assets/images/home/property/payment/bg_cut.png"),
      tipsImg: require("../../../assets/images/home/property/payment/icon_tips.png"),
      arrowRightImg: require("../../../assets/images/home/property/payment/icon_arrow_right_black.png"),
      arrowDownImg: require("../../../assets/images/home/property/payment/icon_arrow_down_black.png"),
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true,
      itemFont: this.$curTheme==0?"14px":"17px",
    };
  },
  components: {
    NavigationBar,
    "property-order-goto-pay": propertyOrderGotoPay,
    "property-pay-popup": propertyPayPopup
  },
  mounted() {},
  /* 跳转积分时缓存页面数据 */
  beforeRouteLeave(to, from, next) {
    if (to.name == "readTextPage") {
      this.needRefresh = false;
    } else {
      this.needRefresh = true;
    }
    next();
  },
  activated() {
    if (this.needRefresh) {
      window.addEventListener("native.keyboardhide", this.pointValid);
      cordova.plugins.Keyboard.hideKeyboardAccessoryBar(false);
      this.billNo = this.$route.params.billCode;
      this.areaCode = this.$route.params.areaCode;
      this.integralDeduction = 0;
      this.pointAviablePersent = 0;
      this.integralAll= false;
      this.queryOrderDetail();
    }
  },
  methods: {
    /* 查询订单详情 */
    queryOrderDetail() {
      var reqData = {
        bill_code: this.billNo
      };
      queryOrderDetailService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          if (result.result) {
            for (let index in result.data.roomList) {
              for (let monthIndex in result.data.roomList[index].monthList) {
                result.data.roomList[index].monthList[
                  monthIndex
                ].isShow = false;
              }
            }
            this.orderDetial = result.data;
            this.bill_amount = result.data.bill_amount;
            this.billCodePoint = result.data.bill_amount;
            
            this.getUseLimit();
          } else {
            this.$toast("网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    //查询用户可用积分
    getUseLimit() {
      var reqData = {
        ownerCode: this.$userInfo.OWNER_CODE,
        money: this.billCodePoint
      };
      getUseLimitService(reqData)
        .then(result => {
          //请求成功
          if (result.code == 200) {
            //true表示返回成功
            this.pointAviable = Math.floor(result.data / 100) * 100;
          } else {
            this.pointAviable = 0;
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 查询月份账单 */
    queryBillDetail(parentIndex, index) {
      var reqData = {
        bill_no:this.billNo,
        room_code: this.orderDetial.roomList[parentIndex].room_code,
        month_code: this.orderDetial.roomList[parentIndex].monthList[index]
          .month_code
      };
      queryPaymentDetailService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          if (result.result) {
            if (result.data.billDetailList) {
              this.orderDetial.roomList[parentIndex].monthList[
                index
              ].billDetailList = result.data.billDetailList;

              this.orderDetial.roomList[parentIndex].monthList[
                index
              ].isShow = !this.orderDetial.roomList[parentIndex].monthList[
                index
              ].isShow;
            } else {
              this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
            }
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 获取某月账单物业费明细 */
    showTime(parentIndex, index) {
      //判断月份详情是否存在，不存在进行网络请求
      if (
        this.orderDetial.roomList[parentIndex].monthList[index]
          .billDetailList == null ||
        this.orderDetial.roomList[parentIndex].monthList[index]
          .billDetailList == "undefined"
      ) {
        this.queryBillDetail(parentIndex, index);
      } else {
        this.orderDetial.roomList[parentIndex].monthList[index].isShow = !this
          .orderDetial.roomList[parentIndex].monthList[index].isShow;
      }
    },
    /* 使用全部积分 */
    integralAllFn(intergrals) {
      this.integralAll = !this.integralAll;
      if (this.integralAll) {
        this.integralDeduction = Math.floor(intergrals / 100) * 100;
      } else {
        this.integralDeduction = 0;
      }
      this.pointAviablePersent = (this.integralDeduction / 100).toFixed(2); //最多可扣积分
      this.billCodePoint = (
        parseFloat(this.bill_amount) - parseFloat(this.pointAviablePersent)
      ).toFixed(2); //实付金额
    },
    /* 积分加 */
    numberAdd(intergral) {
      if (this.integralDeduction < intergral) {
        if (this.integralDeduction + 100 <= intergral) {
          this.integralDeduction = this.integralDeduction + 100;
        } else {
          //如果不按整百扣除，放开此行
          this.$toast("本次可用最高积分为" + this.integralDeduction);
        }
      } else {
        //如果不按整百扣除，放开此行
        this.$toast("本次可用最高积分为" + this.integralDeduction);
      }
      this.pointAviablePersent = (this.integralDeduction / 100).toFixed(2); //最多可扣积分
      this.billCodePoint = (
        parseFloat(this.bill_amount) - parseFloat(this.pointAviablePersent)
      ).toFixed(2); //实付金额
    },
    /* 积分减 */
    numberReduce() {
      this.integralAll = false;
      if (this.integralDeduction == 0) {
        this.$toast("已经是最低了，不能在减少了哦！");
        return;
        this.integralDeduction = 0;
      } else {
        if (this.integralDeduction - 100 < 0) {
          this.integralDeduction = 0;
        } else {
          this.integralDeduction = this.integralDeduction - 100;
        }
      }
      this.pointAviablePersent = (this.integralDeduction / 100).toFixed(2); //最多可扣积分
      this.billCodePoint = (
        parseFloat(this.bill_amount) - parseFloat(this.pointAviablePersent)
      ).toFixed(2); //实付金额
    },
    pointValid(){
      let value = this.integralDeduction;
      if (isNaN(value)) {
        this.integralDeduction=0;
        return;
      }
      if (value > this.pointAviable) {
          this.integralDeduction = Math.floor(this.pointAviable / 100) * 100;
          this.$toast('本单最高抵扣' + this.pointAviable + '积分');
      } else if (value < 0) {
          this.integralDeduction = 0;
          this.$toast('抵扣积分不能为负数');
      } else {
          var num = value;
          this.integralDeduction = Math.floor(value / 100) * 100;
          if (num != this.integralDeduction) {
              this.$toast('抵扣积分为整百');
          }
      }
      this.pointAviablePersent = (this.integralDeduction / 100).toFixed(2); //最多可扣积分
      this.billCodePoint = (
        parseFloat(this.bill_amount) - parseFloat(this.pointAviablePersent)
      ).toFixed(2); //实付金额
    },
    /* 跳转积分说明 */
    integralInstructionFn() {
      this.$router.push({
        name: "readTextPage",
        params: { pageType: 5 }
      });
    },
    onClickLeft() {
      window.removeEventListener("native.keyboardhide", this.pointValid);
      cordova.plugins.Keyboard.hideKeyboardAccessoryBar(true);
      this.$router.back();
    },
    /* 立即支付 */
    goToPay() {
      if(this.billCodePoint < 0){
        this.$toast("订单生成错误，金额不能为负数");
        return;
      }
      let reqData = {
        bill_no: this.billNo,
        point: this.integralDeduction + "",
        decount_money: this.pointAviablePersent,
        pay_money: this.billCodePoint
      };
      payOrderService({ data: JSON.stringify(reqData) })
        .then(result => {
          if (result.result == true) {
            /* 获取支付参数 */
            this.payParam = result.payParam;
            /* 判断支持的支付方式 */
            if (result.payParam.IS_ALI_PAY == "1") {
              this.isAlipay = true;
            }
            if (result.payParam.IS_WX_PAY == "1") {
              this.isWechart = true;
            }
            /* 支付弹窗 */
            this.payWindowShow = true;
            // /*监听ios支付结果 检测当前订单状态*/
            window.broadcaster.addEventListener("paymentBack", this.payCallBack);
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 支付回调 */
    payCallBack() {
      this.checkOrderStatus();
    },
    /* 检查订单状态 */
    checkOrderStatus() {
      /* 取消监听，避免重复监听 */
      window.broadcaster.removeEventListener("paymentBack",this.payCallBack);
      /* 获取订单数据 */
      let reqData = { bill_code: this.billNo };
      queryOrderDetailService({ data: JSON.stringify(reqData) })
        .then(result => {
          if (result.result) {
            if (result.data.bill_status == 1) {
              //订单状态为已支付，跳转支付成功界面
              result.data.billType = "账单缴费";
              this.$router.push({
                name: "paymentPaySuccess",
                params: { orderData: result.data }
              });
            } else {
              //支付失败，返回列表页
              this.$toast({
                duration: 500,
                message: "支付失败",
                onClose: () => {
                  this.$router.back();
                }
              });
            }
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 支付方式弹窗 */
    closePopup() {
      this.payWindowShow = false;
    }
  }
};
</script>

